import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { textHelper } from './../../helpers'
import './UserResult.css';
import { MainImage } from './../MainImage';
import { Button } from './../Button';


export class UserResult extends Component {

  render() {
    const { userInfo } = this.props;
    const resultsData = this.getResultsData(userInfo);

    return <div className="block UserResult">
      <MainImage sticker={ resultsData.sticker }/>

      <div className="indent_vert UserResult__title">
        Поздравляем!
      </div>

      <div className="indent_vert UserResult__text">
        { resultsData.text }
      </div>

      <div className="indent_vert UserResult__values">
        Вы набрали { this.renderValue(userInfo.aValue) } за хайп и { this.renderValue(userInfo.bValue) } за качество.
      </div>

      <div className="indent_vert_double UserResult__actions">
        <Button text="Результаты" onPress={this.props.onShowResults}/>
        <Button text="Играть ещё раз" onPress={this.props.onTryAgain}/>
      </div>
    </div>;
  }


  renderValue(value) {
    const valueModifier = value < 0
      ? 'UserResult__value_neg'
      : (value === 0 ? 'UserResult__value_zero' : 'UserResult__value_pos');
    const valueClass = `UserResult__value`;
    return <span className={valueClass}>
      <span className={valueModifier}>
        { value }
      </span>&nbsp;{ textHelper.getDeclension(value, 'балл', 'балла', 'баллов') }
    </span>;
  }


  getResultsData(userInfo) {
    const aPositive = userInfo.aValue >= 0;
    const bPositive = userInfo.bValue >= 0;

    if (aPositive && bPositive) {
      return {
        text: 'У вас получился умопомрачительный сервис на моднейших технологиях!',
        sticker: 'fox_104'
      };
    } else if (aPositive && !bPositive) {
      return {
        text: 'Внутри всё модно и молодёжно, хотя не все пользователи довольны качеством сервиса.',
        sticker: 'dog_110'
      };
    } else if (!aPositive && bPositive) {
      return {
        text: 'Старые добрые проверенные технологии никогда никого не подводили.',
        sticker: 'dog_103'
      };
    } else {
      return {
        text: 'Похоже, что сервис скоро загнётся.',
        sticker: 'fox_118'
      };
    }
  }

}


UserResult.propTypes = {
  userInfo: PropTypes.object,
  userResult: PropTypes.object,
  onShowResults: PropTypes.func,
  onTryAgain: PropTypes.func
};



